.row {
  margin: 0;
  padding: 0;
}
.outerbox {
  margin: 0;
  padding: 0;
  height: 100vh;
  background: black;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-shadow: -4px 0 20px -2px #000000;
  box-shadow: -4px 0 20px -2px #000000;
  border-radius: 10px 0 0 10px;
}

.nav-container {
  width: auto;
}

.outerbox > .nav-container .nav-link {
  margin: 70px;
  color: white;
  padding: 5px;
  padding-left: 15px;
  font-size: 40px;
  text-align: left;
  border-left: 1px solid white;
  font-family: "Exo 2", sans-serif;
}

.outerbox > .nav-container > a:hover {
  text-decoration: none;
  color: #eded;
}

